import { useState } from "react";
//import { Swiper, SwiperSlide } from "swiper/react";
import { Mousewheel } from "swiper/modules";
import styles from "./Swiper.module.css";

import {
  InputWrapper,
  Selected,
  SwiperSlideStyled,
  SwiperStyled,
  TimePickerContainer,
} from "../EventForm/InputTime/InputTime.styled";

const TimePicker = ({ onSelectTime }) => {
  const [selectedHour, setSelectedHour] = useState(12);

  const hours = Array.from({ length: 24 }, (_, index) => index);

  const handleHourChange = (hour) => {
    setSelectedHour(hour);
  };

  const timeFormat = (time) => {
    return time < 10 ? `0${time}` : time;
  };

  const time = `${selectedHour}`;

  return (
    <TimePickerContainer onClick={() => onSelectTime(time)}>
      <div className={styles.visionLine}></div>
      {/* hour */}
      <SwiperStyled
        direction="vertical"
        slidesPerView={1}
        spaceBetween={0}
        mousewheel={true}
        modules={[Mousewheel]}
        initialSlide={8}
        loop={true}
        onSlideChange={(swiper) => {
          handleHourChange(timeFormat(hours[swiper.activeIndex]));
        }}
      >
        {hours.map((hour) => (
          <SwiperSlideStyled key={hour}>
            {({ isActive }) => (
              <Selected $isActive={isActive}>{timeFormat(hour)}</Selected>
            )}
          </SwiperSlideStyled>
        ))}
      </SwiperStyled>
    </TimePickerContainer>
  );
};

const Test = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [time, setTime] = useState("");

  const onSelectTime = (time) => {
    setTime(time);
  };

  const handleInputChange = () => {
    setIsOpen((prevState) => !prevState);
  };

  return (
    <InputWrapper>
      <div onClick={handleInputChange} className={styles.label}>
        {"Input"}
      </div>
      <button name="time" onClick={handleInputChange} className={styles.input}>
        {time}
      </button>
      {isOpen && <TimePicker onSelectTime={onSelectTime} />}
    </InputWrapper>
  );
};

export default Test;
